.btn {
  font-size: 1rem;
  font-family: "cormorant-garamond", serif;
  font-weight: 400;
  color: #151515;
  line-height: 1;
  padding-top: 0.75rem;
  padding-bottom: 0.625rem;
  border: 1px solid #6F8471;
  display: inline-block;
  text-align: center;
  width: min(100%, 6.4375rem);
  position: relative;
}
@media screen and (min-width: 768px) {
  .btn {
    padding-top: 0.3125rem;
    padding-bottom: 0.1875rem;
  }
}
.btn:before {
  content: "";
  position: absolute;
  top: 57%;
  right: -1.25rem;
  transform: translateY(calc(-50% - 2px)) rotate(30deg);
  width: 0.375rem;
  height: 0.0625rem;
  background: #151515;
  transition: 0.6s;
}
.btn:after {
  content: "";
  position: absolute;
  top: 56%;
  right: -1.25rem;
  transform: translateY(-50%);
  width: 2rem;
  height: 0.0625rem;
  background: #151515;
  transition: 0.6s;
}
.btn:hover:before, .btn:hover:after {
  right: -1.5625rem;
}

.btn--white {
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
}
.btn--white:before, .btn--white:after {
  background: #FFFFFF;
}

